<template>
 <!-- 
    동적인 UI 만드는법:
    0. UI 디자인을 가장먼저 해두고
    1. UI의 현재상태를 데이터로 저장해둠
    2. 데이터에 따라 UI가 어떻게 보일지 작성
 -->
  <div class="black-bg" v-if="모달창열렸니">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" alt="">
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <!-- v-model을 사용하면 입력받은 값을 바로 변수(month)에 넣어줌 -->
      <!-- .number를 넣으면 자동으로 숫자형으로 형변환 -->
      <input v-model.number="month">
      <!-- <textarea v-model="month"></textarea> -->
      <!-- <select v-model="month">
          <option>16</option>
          <option>35235</option>
          <option>3525</option>
          <option>4562</option>
          <option>34345</option>
      </select> -->
      <!-- <input type="checkbox" v-model="month"> -->
      <!-- <input @input="changePrice($event.target.value)"> -->
      <p> {{ month }} 개월 선택함 {{ 원룸들[누른거].price * (month ? month : 1) }}원</p>
      <DiscountBanner :할인율="할인율"/>
      <!-- props는 read-only라서 재할당이 불가능하다. -->
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
import DiscountBanner from './DiscountBanner.vue';

// 부모 컴포넌트와 자식 컴포넌트 모두에서 사용하는 데이터는
// 부모 컴포넌트에 만드는게 좋음
export default { 
    name: 'DetailModal',
    data() {
        return {
            month: 3,
        }
    },
    // Vue 전용 form validation 라이브러리를 사용하면 watch 를 사용하지 않고 간편하게 유효성검사 가능
    watch: {
        month(변경후) {
            if(typeof 변경후 === 'string' && !!변경후) {
                this.month = 3;
                alert('숫자만 입력해!');
            }
        }
    },  
    props: {
        원룸들: Array,
        누른거: String,
        모달창열렸니: Boolean,
        할인율: Number
    },
    beforeUpdate() {
        if(this.month == 2) {
            alert('3개월 이상 가능합니다.');
            this.month = 3
        }
    }, 
    components: {
        DiscountBanner,
    },
    // methods: {
    //     changePrice(inputMonth) {
    //         if(!inputMonth) {
    //             this.month = 1;
    //             return;
    //         }
    //         this.month = Number(inputMonth);
    //     }
    // }
}
</script>

