<template>

<transition name="fade">
<!-- 
  props로 자식에게 데이터 보내는법
  1. 데이터 보내기
  2. 등록하기
  3. 사용하기
 -->
  <DetailModal @closeModal="모달창열렸니 = false" :할인율="할인율" :원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>
</transition>

  <div class="menu">
    <a v-for="product in 메뉴들" :key="product">{{ product }}</a>
  </div>

  <!-- 
    축약해둔 컴포넌트 쓰는 법
    1. vue파일 import 해오고
    2. 등록하고
    3. 쓴다.
   -->
  <DiscountBanner :할인율="할인율" v-if="showDiscount == true"/>

  <button @click="priceSort">가격순정렬</button>&nbsp;
  <button @click="priceReverseSort">가격역순정렬</button>&nbsp;
  <button @click="wordSort">가나다순정렬</button>&nbsp;
  <button @click="sortBack">되돌리기</button>&nbsp;
  
  <!-- 
    props 보낼 때 다양한 자료형 입력가능
    <Discount 작명="문자자료">
    <Discount :작명="숫자자료">
   -->
  <DetailCard @openModal="모달창열렸니 = true; 누른거=$event" :room="원룸들[i]" v-for="(room, i) in 원룸들" :key="i"/>

</template>

<script>
import 원룸들 from './assets/oneroom.js';
import DiscountBanner from './DiscountBanner.vue';
import DetailModal from './DetailModal.vue';
import DetailCard from './DetailCard.vue';

export default {
  name: 'App',
  data() {
    return {
      할인율: 30,
      showDiscount: true,
      원룸들오리지널: [...원룸들],
      오브젝트: { name: 'kim', age: 20 },
      누른거: 0,
      원룸들,
      모달창열렸니: false,
      신고수: [0, 0, 0],
      메뉴들: ['Home', 'Shop', 'About'],
    }
  },
  methods: {
    priceSort() {
      this.원룸들.sort((current_room, next_room) => {
        return current_room.price - next_room.price;
      });
    },
    priceReverseSort() {
      this.원룸들.sort((current_room, next_room) => {
        return next_room.price - current_room.price;
      });
    },
    wordSort() {
      this.원룸들.sort((current_room, next_room) => {
        var current_room_title = current_room.title.toUpperCase(); 
        var next_room_title = next_room.title.toUpperCase();
        if (current_room_title < next_room_title) {
          return -1;
        }
        if (current_room_title > next_room_title) {
          return 1;
        }

        // 이름이 같을 경우
        return 0;
      });
    },
    sortBack() {
      this.원룸들 = [...this.원룸들오리지널];
    },
    increase(index) {
      this.신고수[index]++;
    }
  },
  mounted() {
    setTimeout(() => {
      this.showDiscount = false;
    }, 100000); 
    setInterval(() => {
        this.할인율--;
        if(this.할인율 === 0){
          this.showDiscount = false;
        }
    }, 1000);
  },
  components: {
    DiscountBanner, 
    DetailModal,
    DetailCard,
  }
}
</script>

<style>
/* 
  .애니명-enter-from
  .애니명-enter-active
  .애니명-enter-to
  애니메이션을 주려면 이렇게 총 세개가 필요함
*/
.fade-enter-from {
  transform: translateY(-1000px);
}
.fade-enter-active {
  transition: all 1s;
}
.fade-enter-to {
  transform: translateY(0px);
}

/* 
  창이 꺼질때 설정방법
*/
.fade-leave-from {
  transform: translateY(0px);
}
.fade-leave-active {
  transition: all 1s;
}
.fade-leave-to {
  transform: translateY(-1000px);
}

.start {
  opacity: 0;
  transition: all 1s;
}
.end {
  opacity: 1;
}

body {
  margin: 0;
}

div {
  box-sizing: border-box;
}

.discount {
  background: #eee;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}

.black-bg {
  position: fixed; 
  width: 100%; 
  height: 100%;
  padding: 20px;
  background: rgba(0,0,0,0.5);
}

.white-bg {
  width: 100%; 
  background: white;
  padding: 20px;
  border-radius: 8px;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.menu {
  background: darkslateblue;
  padding: 15px;
  border-radius: 5px;
}

.menu a {
  color: white;
  padding: 10px;
}

.room-img {
  width: 100%;
  margin-top: 40px;
}
</style>
